<style scoped>
.box {
  display: inline-block;
  background: #ddd;
  padding: 20px;
  margin: 20px;

  box-shadow: 10px 10px 5px #888888;
}

.debug {
  color: rgb(114, 76, 76);
  display: none;
}

.icon {
  width: 48px;
  float: right;
}

.dl-href {
  padding-top: 20px;
}

.disable_text {
  color: #aaa;
}

.disable_img {
  filter: gray;
  filter: grayscale(100%);
}
</style>

<template>
  <div class="box" v-show="pack.visible" v-bind:class="{disable_text : ! pack.enable}">
    <h1>
      {{pack.title}}
      <img
        :src="pack.icon"
        class="icon"
        v-bind:class="{disable_img  : ! pack.enable}"
      />
    </h1>

    <div>版本 : {{pack.version}}</div>
    <div>大小 : {{pack.size}}</div>
    <div>发布日期 : {{ pack.date}}</div>
    <div>SHA-256 : {{ pack.sha256sum}}</div>
    <div class="debug">for debug: {{ pack. metaFileUrl}}</div>
    <div class="dl-href" v-show="pack.enable">
      下载 :
      <a :href=" pack.packageFileUrl ">{{ pack. packageFileName }}</a>
    </div>
  </div>
</template>


<script>
import axios from "axios";

export default {
  name: "DownloadInfoBox",

  props: ["value"],

  model: {
    prop: "value",
    event: "balabala"
  },

  data() {
    return {
      pack: {
        visible2: false,
        enable: false,

        title: "XXX 客户端",
        version: "1.22.333333333",
        icon: "https://release.bitwormhole.com/resources/icon/download.png",
        size: "1xx MB",
        date: "1970-01-01",
        sha256sum: "fffffffffffffffffffffffffffffff",
        metaFileUrl: "https://xxxxx",
        packageFileName: "xxxxx.pack",
        packageFileUrl: "https://xxxxx"
      }
    };
  },

  mounted() {
    this.pack.metaFileUrl = this.value.href;
    this.loadMetaInfo(this.pack.metaFileUrl);
  },

  methods: {
    loadMetaInfo(url) {
      var self = this;

      var on_ok = res => {
        var data = res.data;
        self.onMetaReady(data);
      };

      var on_err = () => {};

      axios
        .get(url)
        .then(on_ok)
        .catch(on_err);
    },

    onMetaReady(meta) {
      // var str = JSON.stringify(meta, null, 4);
      this.pack = meta.package;
    }
  }
};
</script>
